.cast_title {
    text-align: center;
    padding: 30px;
    text-transform: capitalize;
    font-size: 48px;
}

.list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
}

.item {
    flex-basis: calc((100% - 80px) / 8);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.item_block {
    padding: 16px;
}

.text_name {
    text-align: center;
    font-size: 20px;
}